export default {
	data() {
		return {
			height: 0,
			caseNo:"",
			case_1:{},
			case_2:{},
			case_3:{}
		}
	},
	onLoad(options) {
		this.caseNo = options.id;
		this.height = uni.getSystemInfoSync().windowHeight;
		uni.showLoading({mask:true});
		this.getData();
	},
	methods: {
		go(url){
			uni.navigateTo({url: url});
		},
		getData(){
			uni.request({
				url: this.apiServer + 'subCabinetType/getSubCabinetType',
				method: 'POST',
				header: {
					'content-Type': 'application/x-www-form-urlencoded',
				},
				success: (res) => {
					if(res.data.status == 200){
						let data = res.data.data;
						for(let i=0;i<data.length;i++){
							data[i].rent = parseFloat(data[i].rent).toFixed(2);
						};
						this.case_1 = data[0];
						this.case_2 = data[1];
						this.case_3 = data[2];
					}else if(res.data.status == 588){
						this.checkLogin();
						uni.showToast({
							title: '登录失效，请再试一次',
							icon: 'none',
							duration: 2000
						});
					}else{
						uni.showToast({
							title: res.data.msg,
							icon: 'none',
							duration: 2000
						});
					}
				},
				fail: (res) => {
					uni.showToast({
						title: '网络异常',
						icon: 'none',
						duration: 2000
					});
					setTimeout(() => {
						uni.navigateBack({url: '../index/index'});
					}, 2000);
				},
				complete: (res) => {
					uni.hideLoading();
				}
			});
		},
		storage(data){
			uni.request({
				url: this.apiServer + 'cabinetProcessing/userOpenCabinet',
				method: 'POST',
				data: {
					cabinetNo: this.caseNo,
					subCabinetTypeNo: data.pkNo
				},
				header: {
					'content-Type': 'application/x-www-form-urlencoded',
					'token': uni.getStorageSync('opid')
				},
				success: (res) => {
					if(res.data.status == 200){
						uni.showToast({
							title: '开柜成功',
							duration: 2000
						});
						setTimeout(() => {
							uni.navigateBack({url: '../index/index'});
						}, 2000);
					}else if(res.data.status == 588){
						this.checkLogin();
						uni.showToast({
							title: '登录失效，请再试一次',
							icon: 'none',
							duration: 2000
						});
					}else{
						uni.showToast({
							title: res.data.msg,
							icon: 'none',
							duration: 2000
						});
					}
				},
				fail: (res) => {
					uni.showToast({
						title: '网络异常',
						icon: 'none',
						duration: 2000
					});
				}
			})
		}
	}
}
